import React from 'react';

function Sentences(props) {
  let { query, filteredArray } = props.results;
  return (
    <div className="notification">
      <div className="title">{query}</div>
      <div className="subtitle is-6">{`${filteredArray.length} result(s)`}</div>
      {filteredArray.length ? (
        filteredArray.map((e, index) => (
          <div key={index}>
            <pre>
              <code>
                {JSON.stringify(
                  {
                    type: 'Phrase',
                    content: `${e['Example Sentence']}`,
                    translation: e.Translation,
                    examples: []
                  },
                  null,
                  4
                )}
              </code>
              {index !== filteredArray.length - 1 ? ',' : ''}
            </pre>
          </div>
        ))
      ) : (
        <h1>Not in the database</h1>
      )}
    </div>
  );
}

export default Sentences;
