import React, { useEffect, useState } from 'react';
import Sentences from './components/Sentences';
import sentences from './utils/sentenceBank.json';
import './style.css';

function App() {
  const [output, setOutput] = useState([]);
  const [word, setWord] = useState('');
  const [queries, setQueries] = useState([]);

  useEffect(() => {}, []);

  const filterQueries = () => {
    let array = [];
    queries.forEach(element => {
      console.log(element.query);
      let temp = sentences.filter(phrase =>
        phrase['Example Sentence'].includes(element.query)
      );
      array.push({ query: element.query, filteredArray: temp });
      // setOutput([...output, { query: element.query, filteredArray: temp }]);
    });
    setOutput(array);
  };

  console.log('---------output', output);

  return (
    <div>
      <section className="hero is-primary is-bold">
        <div className="hero-body">
          <div className="container">
            <h1 className="title">KK's project I forgot name</h1>
            <h2 className="subtitle">pay me pls</h2>
          </div>
        </div>
      </section>

      <section className="section">
        <div className="container">
          <p className="title"> Queries</p>
          <p className="subtitle"> Capitalization doesn't matter</p>
          <div className="flex">
            <input
              className="input inputWidth"
              type="text"
              placeholder="keyword"
              onChange={e => setWord(e.target.value.trim())}
            ></input>
            <div className="buttons mx-3">
              <div
                className="button is-link"
                onClick={() => {
                  setQueries([...queries, { query: word }]);
                }}
              >
                Add Query
              </div>
              <div
                className="button is-success"
                onClick={() => filterQueries()}
              >
                Find All Phrases
              </div>
              <div
                className="button is-danger"
                onClick={() => {
                  setQueries([]);
                  setOutput([]);
                }}
              >
                Clear
              </div>
            </div>
          </div>

          <div className="tags are-large mt-3">
            {queries.map((word, index) => (
              <div key={index} className="tag">
                {word.query}
              </div>
            ))}
          </div>
        </div>
      </section>

      <section className="section">
        <div className="container">
          {output.map((element, index) => (
            <Sentences key={index} results={element} />
          ))}
        </div>
      </section>
    </div>
  );
}

export default App;
